<template>
  <div>
    <v-app-bar
      v-scroll="onScroll"
      fixed
      tag="nav"
      :dense="isScrolling"
      class="px-6"
      elevate-on-scroll
      light
    >
      <v-toolbar-title class="">
        <div class="d-flex">
          <div class="mr-2 align-self-start">
            <v-img
              v-if="app.logo"
              width="100px"
              :src="'/uploads/app/' + app.logo"
            >
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular
                    indeterminate
                    color="primary"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </div>
          <div v-if="app.title" class="align-self-center">
            {{ app.title }}
          </div>
        </div>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-app-bar-nav-icon
        class="hidden-md-and-up"
        @click="drawer = true"
      ></v-app-bar-nav-icon>
      <div v-if="$store.state.loading.menu.menus" class="hidden-sm-and-down">
        <v-skeleton-loader
          v-for="i in 5"
          :key="i"
          type="list-item"
        ></v-skeleton-loader>
      </div>
      <div v-else class="hidden-sm-and-down" style="height:100%">
        <div class="d-flex" style="height:100%">
          <div
            v-for="item in getMenu('home', $auth)"
            :key="item._id"
            class="align-self-stretch"
          >
            <v-btn
              height="100%"
              exact-active-class="primary--text"
              :to="item.route"
              link
              text
              exact
            >
              {{ item.translation ? $t(item.translation) : item.title }}
            </v-btn>
          </div>
          <div style="width:150px" class="ml-2 align-self-stretch d-flex">
            <v-select
              v-model="lang.locale"
              class="align-self-center"
              height="100%"
              :items="$store.state.lang.langs"
              item-text="name"
              item-value="locale"
              prepend-inner-icon="mdi-flag"
              outlined
              color="primary"
              dense
              single-line
              hide-details
              @change="changeLang"
            ></v-select>
          </div>
        </div>
      </div>
    </v-app-bar>
    <v-navigation-drawer
      v-if="drawer"
      v-model="drawer"
      floating
      app
      light
      overflow
      class="d-lg-none"
    >
      <v-toolbar-title>
        <div class="d-flex my-2 text-center justify-center">
          <div v-if="app.logo" class="mr-2">
            <!-- <v-avatar> -->
            <v-img width="100px" :src="'/uploads/app/' + app.logo">
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular
                    indeterminate
                    color="primary"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
            <!-- </v-avatar> -->
          </div>
          <div v-if="app.title" class="align-self-center black--text">
            {{ app.title }}
          </div>
        </div>
      </v-toolbar-title>
      <v-list>
        <v-divider></v-divider>
        <div v-if="$store.state.loading.menu.menus">
          <v-skeleton-loader
            v-for="i in 5"
            :key="i"
            type="list-item"
          ></v-skeleton-loader>
        </div>
        <div v-else>
          <div v-for="item in getMenu('home', $auth)" :key="item._id">
            <v-list-item
              v-if="item.children.length === 0"
              link
              exact
              :to="item.route"
            >
              <v-list-item-action>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>{{
                  item.translation ? $t(item.translation) : item.title
                }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-group
              v-if="item.children.length > 0"
              v-model="item.active"
              :prepend-icon="item.icon"
              no-action
            >
              <template v-slot:activator>
                <v-list-item-content>
                  <v-list-item-title>{{
                    item.translation ? $t(item.translation) : item.title
                  }}</v-list-item-title>
                </v-list-item-content>
              </template>

              <v-list-item
                v-for="(subItem, i) in item.children"
                :key="i"
                link
                exact
                :to="subItem.route"
              >
                <v-list-item-action>
                  <v-icon>{{ subItem.icon }}</v-icon>
                </v-list-item-action>
                <v-list-item-content>
                  <v-list-item-title>
                    {{
                      subItem.translation
                        ? $t(subItem.translation)
                        : subItem.title
                    }}
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-group>
          </div>
        </div>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex'
export default {
  data() {
    return {
      drawer: false,
      isScrolling: false
    }
  },
  computed: {
    ...mapState(['lang']),
    ...mapState('settings', {
      app: (state) => state.settings.app.app
    }),
    ...mapGetters({
      locales: 'lang/locales'
    }),
    ...mapGetters('menu', ['getMenu']),
    locale() {
      return this.$store.state.lang.locale
    }
  },
  methods: {
    ...mapActions('lang', ['fetchLangs', 'fetchTranslation']),
    onScroll() {
      const offset = window.pageYOffset
      this.isScrolling = offset > 50
    },
    changeLang() {
      localStorage.setItem('lang', this.locale)
      location.reload()
    }
  }
}
</script>

<style lang="scss"></style>
